* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  color: #fff;
  background-color: #222;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 30px;
  font-family: Poppins, sans-serif;
  font-size: 1.5rem;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.result-str {
  align-items: center;
  column-gap: 20px;
  margin-top: 10px;
  display: flex;
}

span {
  width: 500px;
  align-items: center;
  transition: all .3s;
  display: flex;
}

span:hover {
  cursor: pointer;
  text-decoration: underline;
}

.form__group {
  width: 20%;
  margin-top: 10px;
  padding: 15px 0 0;
  position: relative;
}

.form__field {
  width: 100%;
  color: #fff;
  background: none;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  padding: 7px 0;
  font-family: inherit;
  font-size: 1.3rem;
  transition: border-color .2s;
}

.form__field::-ms-input-placeholder {
  color: rgba(0, 0, 0, 0);
}

.form__field::placeholder {
  color: rgba(0, 0, 0, 0);
}

.form__field:placeholder-shown ~ .form__label {
  cursor: text;
  font-size: 1.3rem;
  top: 20px;
}

.form__label {
  color: #9b9b9b;
  font-size: 1rem;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 0;
}

.form__field:focus {
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d) 1;
  padding-bottom: 6px;
  font-weight: 700;
  transition: all 1s;
}

.form__field:focus ~ .form__label {
  color: #11998e;
  font-size: 1rem;
  font-weight: 700;
  transition: all .2s;
  display: block;
  position: absolute;
  top: 0;
}

.form__field:required, .form__field:invalid {
  box-shadow: none;
}

button {
  text-transform: uppercase;
  color: #fff;
  min-width: 300px;
  background-image: linear-gradient(to right, #11998e, #38ef7d);
  background-size: 200%;
  border-image-slice: 1;
  border-radius: 15px;
  outline: none;
  margin: 10px;
  padding: 1em 3em;
  transition: all .5s;
}

button:hover {
  background-position: 100%;
}

p {
  margin-left: 10px;
  margin-right: auto;
  font-size: 16px;
}

